.project-meta {
    display: grid;
    grid-template-columns: 270px 1fr;
}

.project-meta .card-media {
    height: 370px;
    border-radius: 5px;
}

.project-meta .card-content {
    padding-top: 0;
    padding-left: 3.25em;
    padding-right: 3.25em;
}

.meta-info {
    display: flex;
    align-items: center;
}

.project-info-list {
    padding: 0!important;
    margin: 0;
    list-style-type: none;
    margin-bottom: 1em;
    padding-left: 0!important;
}

.project-info-list li {
    padding-left: 0;
}

.metric {
    background-color: var(--mui-palette-background-paper);
    border: 1px solid rgba(0,0,0,0.025);
    padding: 1em;
}

.metric .label {
    color: #54B689;
    font-weight: bold;
}

.metric .data {
    font-size: 2em;
    font-weight: bold;
}

.metric .unit {
    font-size: 1rem;
    color: #8f8f8f;
}

.metric .description {
    font-size: 0.875rem;
    color: #8f8f8f;
}

.section-title {
    font-size: 1.25em;
    font-weight: bold!important;
}

@media only screen and (max-width: 425px) {
    .project-meta {
        display: grid;
        grid-template-columns: 1fr;
    }
    .card-content {
        padding-left: 1em!important;
        padding-right: 1em!important;
        padding-top: 1em!important;
    }
}